.footer{
    background-color: #1C1D20;
    z-index: 2;
}

.wrap-contact{
    width: 60%;
}

.wrap-contact p:nth-child(1),
.wrap-contact p:nth-child(2){
    font-size: 4em;
    line-height: 0.7;
}

.wrap-contact p:nth-child(2){
    margin-left: 70px;
    /* margin-top: -40px; */
    margin-bottom: 30px;
}

.wrap-contact .btn{
    padding: 15px;
    margin-bottom: 20px;
    transition: 0.5s;
}

.wrap-contact .wrap-nav-contact{
    right: 10%;
    top: 20%;
    width: 200px;
}

.wrap-contact .wrap-nav-contact a{
    width: 100px;
    height: 100px;
    margin-top: 20px;
}

.wrap-all-info{
    border: solid 3px green;
}

.bottom{
    height: 100px;
    padding: 0px 4%;
    background-color: #1C1D20;
}

.bottom .title{
    font-size: 14px;
}

@media only screen and (min-width: 768px) and (max-width: 1024px){    
    .wrap-contact{
        width: 80%;
    }

    .wrap-contact p:nth-child(1),
    .wrap-contact p:nth-child(2){
        font-size: 3em;
    }

    
    .wrap-contact .wrap-nav-contact{
        right: 0;
    }
}

@media only screen and (max-width: 767px){     
    .wrap-contact{
        width: 60%;
    }
    
    .wrap-contact{
        width: 90%;
        height: auto;
    }

    .wrap-contact a{
        width: 100%;
        font-weight: 300;
    }
    
    .wrap-contact p:nth-child(1),
    .wrap-contact p:nth-child(2){
        font-size: 2.5em;
    }
    
    .wrap-contact p:nth-child(2){
        margin-left: 43px;
        /* margin-top: -30px; */
    }
    
    .wrap-contact .wrap-nav-contact{
        width: 100%;
        position: static !important;
    }
    
    .wrap-contact .wrap-nav-contact a{
        width: 100px;
        height: 100px;
        margin-top: 20px;
    }

    .wrap-contact .wrap-nav-contact div{
        font-weight: 300;
    }
    
    .bottom .title{
        font-size: 14px;
    }
}

@media only screen and (max-width: 280px){
    .wrap-contact p:nth-child(1),
    .wrap-contact p:nth-child(2){
        font-size: 2em;
    }
}